<!--商品分类页 图片上传没做-->
<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/productCategory/list" type='list'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px"  @submit.native.prevent>
         <el-form-item label="上级分类：" prop="nameAlias">
         <el-cascader :options="options" :props="props" :change-on-select='true' 
          v-model="formData.parentIds" :disabled="true">
          </el-cascader>
        </el-form-item>
        <el-form-item label="分类名称：" prop="cateName">
          <el-input maxlength="150" @change="getCateName" v-model="formData.cateName"></el-input>
        </el-form-item>
        <el-form-item label="分类别名：" prop="nameAlias">
          <el-input maxlength="150" v-model="formData.nameAlias"></el-input>
          <span class="gray">设置分类别名，商城显示优先显示别名</span>
        </el-form-item>
        <el-form-item label="保证金：" prop="bondPrice">
          <el-input maxlength="10" v-model="formData.bondPrice"></el-input>
          <span class="gray">设置商家入住时主营类目需要缴纳的保证金</span>
        </el-form-item>
        <el-form-item label="佣金比例：" prop="commissionRate">
          <el-input maxlength="10" v-model.number="formData.commissionRate">
            <template slot="append">%</template>
          </el-input>
          <span class="gray">设置商家入住时经营分类需要支付给平台的费用比例</span>
        </el-form-item>
        <el-form-item label="是否显示：" prop="show">
          <cms-switch v-model="formData.show"></cms-switch>
          <span class="gray">设置该分类是否显示在商城分类导航中</span>
        </el-form-item>
        <!-- <el-form-item label="是否为虚拟分类：" prop="virtual">
          <cms-switch v-model="formData.virtual"></cms-switch>
        </el-form-item> -->
        <el-form-item label="商品类型：" prop="productTypeId">
          <el-select v-model="formData.productTypeId" filterable>
            <el-option v-for="item in productTypeItems" :label="item.name" :value="item.id" :key="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分类图标：" prop="resourcesSpaceId">
           <cms-picture-zone  :data='formData.resourcesSpace' v-model="formData.resourcesSpaceId">
             <cms-button size='small' type='info' icon='correlation'>选择图标</cms-button>
           </cms-picture-zone>
          <!-- 选择图标组件 -->
        </el-form-item>
        <el-form-item label="分类页标题：" prop="cateTitle">
          <el-input maxlength="150" v-model="formData.cateTitle"></el-input>
        </el-form-item>
        <el-form-item label="分类页关键字：" prop="cateKewords">
          <el-input maxlength="255" v-model="formData.cateKewords"></el-input>
        </el-form-item>
        <el-form-item label="分类页描述：" prop="cateDescription">
          <el-input maxlength="255" type='textarea' :rows='4' v-model="formData.cateDescription"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
         <cms-button  v-prem="'/productCategory/update'" @click="update(false)" v-color >修改</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { productTypeList } from '@/api/product/type'
import { productCategoryTree, productCategoryGet, productCategoryUpdate } from '@/api/product/category.js'
var defaultData = {
  'parentId': '',
  'parentIds': [0],
  'productTypeId': '',
  'templatePath': '/default',
  'resourcesSpaceId': ''
}
export default {
  mixins: [form],
  data() {
    return {
      loading: false,
      options: [],
      props: {
        value: 'id',
        label: 'cateName',
        children: 'childs',
        disabled: 'isAuth'
      },
      roleList: ['超级管理员', '运营'],
      formData: Object.assign({}, defaultData),
      rules: {
        cateName: [rules.required()],
        show: [rules.required()],
        virtual: [rules.required()],
        productTypeId: [rules.required('必须选择一个类型', 'blur')],
        bondPrice: [rules.required(), rules.decimal()],
        commissionRate: [rules.required(), rules.number()],
        resourcesSpaceId: [rules.required('请选择图标', 'change')]
      },
      productTypeItems: []
    }
  },
  methods: {
    getCateName(val) {
      this.formData.cateTitle = val
      this.formData.cateKewords = val
      this.formData.cateDescription = val
    },
    rest() { // 修改表单重置
      this.$refs.form.resetFields()
      this.destroyed()
      this.getFormData(this.$route.query.id)
    },
    getFormData(id) { // 获取分类信息
      productCategoryGet({ id: id }).then(res => {
        this.formData = res.data
        if (res.data.nodeIds.length <= 1) {
          this.formData.parentIds = [0]
        } else {
          this.formData.parentIds = res.data.nodeIds.slice(0, -1)
        }
      })
    },
    getMenuTree() { // 获取菜单节点
      productCategoryTree().then(
        res => {
          const root = [{ id: 0, cateName: '根目录' }]
          this.options = root.concat(res.data)
        }
      )
    },
    getProductTypeItems() {
      productTypeList().then(res => {
        this.productTypeItems = res.data
      })
    },
    update() {
      if (this.formData.resourcesSpaceId === '') {
        this.$errorMessage('请至少选择一个图标')
      } else {
        const params = Object.assign({}, this.formData)
        params.parentId = params.parentIds[params.parentIds.length - 1]
        this.updateForm(productCategoryUpdate, params, '/productCategory/list', 'list')
      }
    },
    getAll() {
      this.getFormData(this.$route.query.id)
      this.getMenuTree()
      this.getProductTypeItems()
    }
  },
  created() {
    this.formData = Object.assign({}, defaultData)
    this.getAll()
  }
}
</script>




